@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-2599 {
  @media (max-width: ($vp-2600 - 0.06em)) {
    @content;
  }
}

@mixin vp-2099 {
  @media (max-width: ($vp-2100 - 0.06em)) {
    @content;
  }
}

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 0.06em)) {
    @content;
  }
}

@mixin vp-1699 {
  @media (max-width: ($vp-1700 - 0.06em)) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: ($vp-1440 - 0.06em)) {
    @content;
  }
}

@mixin vp-1366 {
  @media (max-width: ($vp-1366 - 0.06em)) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: ($vp-1280 - 0.06em)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 0.06em)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 0.06em)) {
